import React from 'react';
import {Link} from 'react-router-dom'
const CodeOfConduct = () => {
    return (
        <>
            <div className="container mt-4">
                <div className="row">
                    <div className="col-lg-2"></div>
                    <div className="col-lg-8">
                        <h1>Code of Conduct</h1>
                        <p>
                        All participants of the <Link to="/">Devorzo</Link> Community are expected to abide by our Code of Conduct.
                        </p>
                        <h2 className="mt-4 mb-3">Our Pledge</h2>
                        <p>
                            In the interest of fostering an open and welcoming environment, 
                            we as members, contributors, and leaders of <Link to="/">Devorzo</Link> pledge to make participation in our
                            community a harassment-free experience for everyone, regardless of age, body
                            size, visible or invisible disability, ethnicity, sex characteristics, gender
                            identity and expression, level of experience, education, socio-economic status,
                            nationality, personal appearance, race, religion, or sexual identity
                            and orientation.
                        </p>
                        <p>
                            We pledge to act and interact in ways that contribute to an open, welcoming,
                            diverse, inclusive, and healthy community.
                        </p>    
                        <h2 className="mt-4 mb-3">Our Standards</h2>
                        <p>
                            Examples of behavior that contributes to a positive environment for our
                            community include:
                        </p>
                        <ul>
                            <li>Using welcoming and inclusive language</li>
                            <li>Being respectful of differing viewpoints and experiences</li>
                            <li>Referring to people by their preferred pronouns and using gender-neutral pronouns when uncertain</li>
                            <li>Gracefully accepting constructive criticism</li>
                            <li>Demonstrating empathy and kindness toward other community members</li>
                            <li>Being respectful of differing opinions, viewpoints, and experiences</li>
                            <li>Accepting responsibility and apologizing to those affected by our mistakes,
                                and learning from the experience</li>
                            <li>Focusing on what is best for the community</li>
                        </ul>

                        <p>Examples of unacceptable behavior include:</p>

                        <ul>
                            <li>The use of sexualized language or imagery, and sexual attention or
                                advances of any kind</li>
                            <li>Trolling, insulting or derogatory comments, and personal or political attacks</li>
                            <li>Public or private harassment</li>
                            <li>Publishing othersâ€™ private information, such as a physical or email
                                address, without their explicit permission</li>
                            <li>Other conduct which could reasonably be considered inappropriate in a
                                professional setting</li>
                            <li>Dismissing or attacking inclusion-oriented requests</li>
                        </ul>

                        {/* <h2 className="mt-4 mb-3">Enforcement Responsibilities</h2>
                        <p>
                            Community leaders are responsible for clarifying and enforcing our standards of
                            acceptable behavior and will take appropriate and fair corrective action in
                            response to any behavior that they deem inappropriate, threatening, offensive,
                            or harmful.
                        </p>
                        <p>
                            Community leaders have the right and responsibility to remove, edit, or reject
                            comments, commits, code, wiki edits, issues, and other contributions that are
                            not aligned to this Code of Conduct, and will communicate reasons for moderation
                            decisions when appropriate.
                        </p> */}
                        {/* <h2 className="mt-4 mb-3">Scope</h2>
                        <p>
                            This Code of Conduct applies within all community spaces, and also applies when
                            an individual is officially representing the community in public spaces.
                        </p>
                        <p>
                            Examples of representing our community include using an official e-mail address,
                            posting via an official social media account, or acting as an appointed
                            representative at an online or offline event.
                        </p> */}
                        <h2 className="mt-4 mb-3">Enforcement</h2>
                        <p>
                        Violations of the Code of Conduct may be reported by contacting the team via the <Link to="/report">abuse report form</Link>. 
                        All reports will be reviewed and investigated 
                        and will result in a response that is deemed necessary and appropriate to the 
                        circumstances. Further details of specific enforcement policies may be posted separately.
                        </p>
                        <p>
                        Moderators have the right and responsibility to remove comments or other contributions that are not aligned to this Code of Conduct, or to suspend temporarily or permanently any members for other behaviors that they deem inappropriate, threatening, offensive, or harmful.
                        </p>

                        <h2 className="mt-4 mb-3">Attribution</h2>
                        <p>
                            This Code of Conduct is adapted from
                            
                            <ul>
                                <li>
                                    <a href="https://www.contributor-covenant.org/version/2/0/code_of_conduct.html">
                                    Contributor Covenant version 2.0</a>
                                </li>
                                <li><a href="https://www.writespeakcode.com/code-of-conduct/">Write/Speak/Code</a></li>
                                <li><a href="https://geekfeminismdotorg.wordpress.com/about/code-of-conduct/">Geek Feminism</a></li>
                            </ul>
                             
                        </p>
                    </div>
                </div>
            </div>

            
        </>
    );
}

export default CodeOfConduct;
